@media screen and (max-width: 767px), screen and (max-device-width: 767px) {
  body{
    .footer{
      &__nav{
        .container{
          .row{
            .col-xs-12{
              ul{
                display: flex;
                flex-direction: column;
                align-items: center;
              }
            }
          }
        }
      }
      &__info{
        .container{
          .row{
            .col-xs-12{
              .copyright,
              .payments{
                display: flex;
                flex-direction: column;
                align-items: center;
              }
            }
          }
        }
      }
    }
  }
}